<template>
  <div class="container">
    <h1 class="title">任务详情</h1>
    <div class="columnBox">
      <div class="left">
        <h2>需求详情</h2>
        <table border="1">
          <tr>
            <th>任务名</th>
            <td>{{taskInfo.task_name}}</td>
            <th>需求人员</th>
            <td>{{taskInfo.demand_people}}</td>
          </tr>
          <tr>
            <th>翻译语言</th>
            <td>{{taskInfo.translation_language}}</td>
            <th>紧急程度</th>
            <td>{{taskInfo.urgency_degree}}</td>
          </tr>
        </table>
        <table border="1">
          <tr>
            <th>要求</th>
            <td class="required">{{taskInfo.requirement}}</td>
          </tr>
        </table>
        <a :href="taskInfo.content_url" v-if="taskInfo.content_url" class="downloadButton">下载需求附件</a>
      </div>
      <div class="right">
        <h2>翻译详情</h2>
        <table border="1">
          <tr>
            <th>翻译人员</th>
            <td>{{taskInfo.translate_people}}</td>
            <th>指派时间</th>
            <td>{{taskInfo.assign_time}}</td>
          </tr>
          <tr>
            <th>提交次数</th>
            <td>{{taskInfo.submit_num}}</td>
            <th></th>
            <td></td>
          </tr>
        </table>
        <a :href="taskInfo.content_url" v-if="taskInfo.content_url" class="downloadButton">下载翻译附件</a>
      </div>
    </div>
  </div>
</template>

<script>
    const language = {
      'CN': '简体中文',
      'HK': '繁体中文-香港',
      'TW': '繁体中文-台湾',
      'EN': '英语',
      'ID': '印尼语',
      'TH': '泰语',
      'MY': '马来语',
      'UA': '阿拉伯语'
    }
    const degree = ["低","中","高"];
    export default {
      name: "checkTaskInfo",
      data() {
        return {
          taskInfo: {
            task_name: '',
            demand_people: '',
            translation_language: '',
            urgency_degree: '',
            requirement: '',
            translate_people: '',
            assign_time: '',
            submit_num: '',
            content_url: '',
            translate_url: ''
          }
        }
      },
      methods: {
        init(id) {
          let url = '/v1/translations/'+id;
          let that = this;
          this.$axios.get(url).then((res) => {
            if(res.data.code == 200){
              let task = res.data.data;
              that.taskInfo.task_name = task.title;
              that.taskInfo.demand_people = task.username;
              that.taskInfo.translation_language = language[task.t_language];
              that.taskInfo.urgency_degree = degree[task.level];
              that.taskInfo.requirement = task.remark;
              that.taskInfo.translate_people = task.fix_name;
              that.taskInfo.assign_time = task.fix_time;
              that.taskInfo.content_url = task.content_url;
              that.taskInfo.translate_url = task.translate_url;
              that.taskInfo.submit_num = task.count;
            }
          }).catch((err) => {
            that.$message({
              type: 'error',
              message: '获取数据失败!'
            });
          })
        }
      },
      created() {
         let row = this.$route.query;
         this.init(row.task_id);
      }
    }
</script>

<style scoped lang="stylus">
  .container
    padding 30px
    position relative
    .columnBox
      display flex
      .downloadButton
        display inline-block
        padding: 10px 20px;
        font-size: 14px;
        border-radius: 4px;
        color: #fff;
        background-color: #03B8CC;
        border-color: #03B8CC;
        line-height: 1
      h2
        margin 20px 0
      table
        border-collapse collapse
        margin-bottom: 20px
        th
          width: 100px
          padding 10px
          background: #ccc
        td
          width: 150px
          padding: 10px
        .required
          width: 442px
      .left
        flex 1
      .right
        flex 1
</style>



